<template>
    <v-row>
        <v-col cols="12" md="8">
            <div>
                <v-alert type="success" dismissible v-if="issue==false" style="width: 80%; margin-left:10%">
                    {{this.messageSucces}}
                </v-alert>
                <div v-if="showAdd">
                    <v-alert type="warning" dismissible v-if="issue" style="width: 80%; margin-left:10%">
                        {{this.messageError}}
                    </v-alert>

                    <v-card class="mx-auto" style="width: 80%; height: 100%">
                        <v-card-title>
                            Formulario de inscripcin de alumno
                        </v-card-title>
                    <v-form>
                        <v-container>
                            <v-row>
                                <v-col
                                        cols="12"
                                        md="4"
                                >
                                    <v-text-field
                                            v-model="json.name"
                                            label="Nombre"
                                            required
                                    ></v-text-field>
                                </v-col>

                                <v-col
                                        cols="12"
                                        md="4"
                                >
                                    <v-text-field
                                            v-model="json.last_name"
                                            label="Apellido paterno"
                                            required
                                    ></v-text-field>
                                </v-col>

                                <v-col
                                        cols="12"
                                        md="4"
                                >
                                    <v-text-field
                                            v-model="json.second_name"
                                            label="Apellido materno"
                                            required
                                    ></v-text-field>
                                </v-col>

                                <v-col
                                        cols="12"
                                        md="4"
                                >
                                    <v-text-field
                                            v-model="json.rut"
                                            :counter="12"
                                            label="Rut"
                                            v-mask="'##.###.###-X'"
                                            required
                                    ></v-text-field>
                                </v-col>

                                <v-col
                                        cols="12"
                                        md="4"
                                >
                                    <v-autocomplete
                                        label="Carrera"
                                        :items="items"
                                        v-model="json.career"
                                        hint="Escriba la carrera para filtrar" 
                                        >

                                            <template v-slot:item="data">
                                                <template v-if="typeof data.item !== 'object'">
                                                    <v-list-item-content v-text="data.item"></v-list-item-content>
                                                </template>
                                            <template v-else>
                                                <v-list-item-content>
                                                <v-list-item-title v-html="'Carrera: ' + data.item"></v-list-item-title>
                                                </v-list-item-content>
                                            </template>
                                            </template>
                                            
                                    </v-autocomplete>



                                </v-col>

                                <v-col
                                        cols="12"
                                        md="4"
                                >

                                    <v-autocomplete
                                        label="Facultad"
                                        :items="items2"
                                        v-model="json.department"
                                        hint="Escriba la facultad para filtrar" 
                                        >

                                            <template v-slot:item="data">
                                                <template v-if="typeof data.item !== 'object'">
                                                    <v-list-item-content v-text="data.item"></v-list-item-content>
                                                </template>
                                            <template v-else>
                                                <v-list-item-content>
                                                <v-list-item-title v-html="'Facultad: ' + data.item"></v-list-item-title>
                                                </v-list-item-content>
                                            </template>
                                            </template>
                                            
                                    </v-autocomplete>
                                </v-col>

                                <v-col
                                        cols="12"
                                        md="4"
                                >
                                    <v-btn text color="blue" v-on:click="postStudent">
                                        <v-icon>mdi-account-plus</v-icon>
                                        Guardar
                                    </v-btn>

                                </v-col>

                            </v-row>
                        </v-container>
                    </v-form>
                    </v-card>
                </div>
            </div>
        </v-col>
    </v-row>
</template>

<script>
    import {mapMutations, mapState} from 'vuex';
    import axios from 'axios'
    export default {

        data: () => ({
            json: {
                rut: null,
                name: null,
                last_name: null,
                second_name: null,
                career: null,
                department: null,
                active: true
            },
            message: {
                rut: "El campo de rut se encuentra vaco.",
                rutLength: "El largo del rut debe ser de 12",
                name: "El campo de nombre se encuentra vaco.",
                last_name: "El campo de apellido paterno se encuentra vaco.",
                second_name: "El campo de apellida materno se encuentra vaco.",
                career: "El campo de carrera no ha sido seleccionado.",
                department: "El campo de departamento no ha sido seleccionado.",
                nice: "El alumno ha sido agregado exitosamente.",
                exist: "El rut ingresado ya existe."
            },
                items: [
                    "INGENIERA CIVIL EN ELECTRICIDAD"
                    ,"INGENIERA CIVIL EN GEOGRAFA"
                    ,"INGENIERA CIVIL EN INDUSTRIA"
                    ,"INGENIERA CIVIL EN INFORMTICA"
                    ,"INGENIERA CIVIL EN MECNICA"
                    ,"INGENIERA CIVIL EN METALURGIA"
                    ,"INGENIERA CIVIL EN MINAS"
                    ,"INGENIERA CIVIL EN OBRAS CIVILES"
                    ,"INGENIERA CIVIL EN QUMICA"
                    ,"INGENIERA CIVIL BIOMDICA"
                    ,"INGENIERA CIVIL EN TELEMTICA"
                    ,"INGENIERA CIVIL MECATRNICA"
                    ,"INGENIERA CIVIL EN BIOTECNOLOGA"
                    ,"INGENIERA CIVIL EN AMBIENTE"
                    ,"INGENIERA DE EJECUCIN EN ELECTRICIDAD"
                    ,"INGENIERA DE EJECUCIN EN GEOMENSURA"
                    ,"INGENIERA DE EJECUCIN EN INDUSTRIA"
                    ,"INGENIERA DE EJECUCIN EN COMPUTACIN E INFORMTICA"
                    ,"INGENIERA DE EJECUCIN EN MECNICA"
                    ,"INGENIERA DE EJECUCIN EN METALURGIA"
                    ,"INGENIERA DE EJECUCIN EN MINAS"
                    ,"INGENIERA DE EJECUCIN EN QUMICA"
                    ,"INGENIERA DE EJECUCIN EN CLIMATIZACIN (CALEFACCIN, REFRIGERACIN Y AIRE ACONDICIONADO)"
                    ,"BACHILLERATO EN CIENCIAS Y HUMANIDADES"
                    ,"ADMINISTRACIN PBLICA"
                    ,"INGENIERA COMERCIAL"
                    ,"CONTADOR PBLICO Y AUDITOR (DIURNO)"
                    ,"CONTADOR PBLICO Y AUDITOR (VESPERTINO)"
                    ,"INGENIERA FSICA"
                    ,"INGENIERA MATEMTICA"
                    ,"INGENIERA ESTADSTICA"
                    ,"LICENCIATURA EN CIENCIA DE LA COMPUTACIN"
                    ,"PEDAGOGA EN MATEMTICA Y COMPUTACIN"
                    ,"PEDAGOGA EN FSICA Y MATEMTICA"
                    ,"BIOQUMICA Y LICENCIATURA EN BIOQUMICA"
                    ,"QUMICA Y LICENCIATURA EN QUMICA"
                    ,"PEDAGOGA EN QUMICA Y BIOLOGA"
                    ,"TCNICO UNIVERSITARIO EN ANLISIS QUMICO Y FSICO (DIURNO)"
                    ,"TCNICO UNIVERSITARIO EN ANLISIS QUMICO Y FSICO (VESPERTINO)"
                    ,"QUMICA Y FARMACIA"
                    ,"PEDAGOGA EN CASTELLANO"
                    ,"PEDAGOGA EN INGLS"
                    ,"PEDAGOGA EN FILOSOFA"
                    ,"LICENCIATURA EN HISTORIA"
                    ,"PEDAGOGA EN HISTORIA Y CIENCIAS SOCIALES"
                    ,"PEDAGOGA EN EDUCACIN GENERAL BSICA"
                    ,"LICENCIATURA EN LINGSTICA APLICADA A LA TRADUCCIN EN INGLS - JAPONS E INGLS - PORTUGUS"
                    ,"LICENCIATURA EN ESTUDIOS INTERNACIONALES"
                    ,"PERIODISMO"
                    ,"PSICOLOGA"
                    ,"ARQUITECTURA"
                    ,"PUBLICIDAD"
                    ,"INGENIERA DE ALIMENTOS"
                    ,"INGENIERA EN AGRONEGOCIOS"
                    ,"TECNOLOGA EN ADMINISTRACIN DE PERSONAL"
                    ,"TECNOLOGA EN ALIMENTOS"
                    ,"TECNOLOGA EN CONSTRUCCIONES"
                    ,"TECNOLOGA EN CONTROL INDUSTRIAL"
                    ,"TECNOLOGA EN DISEO INDUSTRIAL"
                    ,"TECNOLOGA EN AUTOMATIZACIN INDUSTRIAL"
                    ,"TECNOLOGA EN MANTENIMIENTO INDUSTRIAL"
                    ,"TECNOLOGA EN TELECOMUNICACIONES"
                    ,"DISEO EN COMUNICACIN VISUAL"
                    ,"MEDICINA"
                    ,"ENFERMERA"
                    ,"OBSTETRICIA Y PUERICULTURA"
                    ,"PEDAGOGA EN EDUCACIN FSICA"
                    ,"LICENCIATURA EN CIENCIAS DE LA ACTIVIDAD FSICA / ENTRENADOR DEPORTIVO"
                    ,"LICENCIATURA EN CIENCIAS DE LA ACTIVIDAD FSICA / TERAPEUTA EN ACTIVIDAD FSICA Y SALUD"
                    ,"KINESIOLOGA"
                    ,"TERAPIA OCUPACIONAL"
                    ,"DERECHO"],

            items2: ["FACULTAD DE INGENIERA", "PROGRAMA DE BACHILLERATO", "FACULTAD DE ADMINISTRACIN Y ECONOMA", 
                     "FACULTAD DE CIENCIA","FACULTAD DE QUMICA Y BIOLOGA","FACULTAD DE HUMANIDADES","ESCUELA DE ARQUITECTURA",
                     "FACULTAD TECNOLGICA","FACULTAD DE CIENCIAS MDICAS","FACULTAD DE DERECHO"],
            messageError: null,
            messageSucces: null,
            issue: null,
            pending: null,
            scoreEsc: {
                rutCorrector: "",
                subject: "",
                scoreObtained: [],
                status: []

            },
            scoreMat: {
                rutCorrector: "",
                subject: "",
                scoreObtained: [],
                status: []

            }

        }),

        computed:
            {
                ...mapState(['showAdd', 'allStudents', 'lastStudent'])
            },

        created: function () {
            // eslint-disable-next-line no-console
            //console.log("holir")
        },

        methods: {
            ...mapMutations(['showAddStudentFunct', 'changeLastStudent']),

            reviewData(){
                if (this.json.name == null){
                    this.issue = true
                    this.messageError = this.message.name
                }
                else if (this.json.department == null) {
                    this.issue = true
                    this.messageError = this.message.department
                }
                else if (this.json.last_name == null){
                    this.issue = true
                    this.messageError = this.message.last_name;
                }
                else if (this.json.second_name == null){
                    this.issue = true
                    this.messageError = this.message.second_name;
                }
                else if (this.json.rut == null){
                        this.issue = true
                        this.messageError = this.message.rut;
                    }
                else if (this.json.career == null){
                    this.issue = true
                    this.messageError = this.message.career;
                }
                else if (this.json.rut.length < 12){
                    this.issue = true
                    this.messageError = this.message.rutLength;
                    }
                else{
                    this.issue = false;
                    this.messageSucces = this.message.nice

                    for (let i=0; i< this.allStudents.length; i++)
                    {
                        if(this.allStudents[i].rut == this.json.rut)
                        {   
                            this.issue = true;
                            this.messageError = this.message.exist
                            break;
                        }
                    }
                }
            },

            sleep (time) {
                return new Promise((resolve) => setTimeout(resolve, time));
            },
            postStudent() {
                this.reviewData()
                if (this.issue == false) {
                    axios.post(this.serverRoute + '/student/post', this.json)
                        .then(response => {
                            // eslint-disable-next-line no-console
                            console.log(response);
                        })
                        .catch(function (error) {
                            // eslint-disable-next-line no-console
                            console.log(error);
                        });


                    this.sleep(500).then(() => {
                        this.showAddStudentFunct(false);
                        this.pushRestore()
                    });

                }

                else{
                    // eslint-disable-next-line no-console
                    //console.log("hay un error", this.messageError)
                }

            },

            pushRestore(){
                this.allStudents.push(this.json)
                // eslint-disable-next-line no-console
                //console.log("miaaaaaau", this.json.rut)
                this.changeLastStudent(this.json.rut)
                this.json ={
                    rut: null,
                    name: null,
                    last_name: null,
                    second_name: null,
                    career: null,
                    department: null,
                    active: true
                }
            },
        }
    }
</script>

<style scoped>


</style>