<template>
            <v-data-table
                    :headers="headers"
                    :search="search"
                    :items="users"
                    loading="load"
                    loading-text="Cargando"
                    class="elevation-3 	"
            >
                <template v-slot:top>
                    <v-toolbar flat color="#191970">
                        <v-toolbar-title style="color:white">Estudiantes</v-toolbar-title>
                        <v-divider
                                class="mx-4"
                                inset
                                style="background-color:white"
                                vertical
                        />
                        <v-text-field
                                v-model="search"
                                append-icon="search"
                                label="Buscar estudiante por rut"
                                dark=""
                                style="margin-left:13%; margin-right:11%"
                                single-line
                                hint="Ingrese el rut con puntos y guin. Ejemplo: 10.000.000-1 o 1.000.000-1"
                        />
                        <v-spacer></v-spacer>
                        <v-dialog v-model="dialog" max-width="600px">
                            <template v-slot:activator="{ on }">
                                <v-btn outlined text color="white" dark rounded v-on="on"> Agregar <v-icon color="blue" class="ml-3">mdi-account-plus-outline</v-icon> </v-btn>
                            </template>
                            <v-card class="mx-auto">
                                <v-card-title>
                                    Formulario de inscripcin de estudiante
                                </v-card-title>
                                <v-form>
                                    <v-container>
                                        <v-row>
                                            <v-col
                                                    cols="12"
                                                    md="4"
                                            >
                                                <v-text-field
                                                        v-model="json.name"
                                                        label="Nombre"
                                                        required
                                                />
                                            </v-col>

                                            <v-col
                                                    cols="12"
                                                    md="4"
                                            >
                                                <v-text-field
                                                        v-model="json.last_name"
                                                        label="Apellido paterno"
                                                        required
                                                />
                                            </v-col>

                                            <v-col
                                                    cols="12"
                                                    md="4"
                                            >
                                                <v-text-field
                                                        v-model="json.second_name"
                                                        label="Apellido materno"
                                                        required
                                                />
                                            </v-col>

                                            <v-col
                                                    cols="12"
                                                    md="4"
                                            >
                                                <v-text-field
                                                    v-model="json.rut"
                                                    :counter="12"
                                                    label="Rut"
                                                    v-mask="['#.###.###-X','##.###.###-X']"
                                                    hint="Ejemplo: 11.111.111-1 o 1.111.111-1" 
                                                    required
                                                />
                                            </v-col>

                                            <v-col
                                                        cols="12"
                                                        md="4"
                                                >
                                                    <v-autocomplete
                                                        label="Carrera"
                                                        :items="items"
                                                        v-model="json.career"
                                                        hint="Escriba la carrera para filtrar" 
                                                        >

                                                            <template v-slot:item="data">
                                                                <template v-if="typeof data.item !== 'object'">
                                                                    <v-list-item-content v-text="data.item"></v-list-item-content>
                                                                </template>
                                                            <template v-else>
                                                                <v-list-item-content>
                                                                <v-list-item-title v-html="'Carrera: ' + data.item"></v-list-item-title>
                                                                </v-list-item-content>
                                                            </template>
                                                            </template>
                                                            
                                                    </v-autocomplete>



                                                </v-col>

                                                <v-col
                                                        cols="12"
                                                        md="4"
                                                >

                                                    <v-autocomplete
                                                        label="Facultad"
                                                        :items="items2"
                                                        v-model="json.department"
                                                        hint="Escriba la facultad para filtrar" 
                                                        >

                                                            <template v-slot:item="data">
                                                                <template v-if="typeof data.item !== 'object'">
                                                                    <v-list-item-content v-text="data.item"></v-list-item-content>
                                                                </template>
                                                            <template v-else>
                                                                <v-list-item-content>
                                                                <v-list-item-title v-html="'Facultad: ' + data.item"></v-list-item-title>
                                                                </v-list-item-content>
                                                            </template>
                                                            </template>
                                                            
                                                    </v-autocomplete>
                                                </v-col>


                                            <v-col
                                                    cols="12"
                                                    md="4"
                                            >
                                                <v-btn text color="blue" v-on:click="postStudent">
                                                    <v-icon>mdi-account-plus</v-icon>
                                                    Guardar
                                                </v-btn>
                                            </v-col>
                                        </v-row>
                                    </v-container>
                                </v-form>
                            </v-card>
                        </v-dialog>
                        <v-dialog v-model="dialogEdit" max-width="600px">
                            <v-card class="mx-auto">
                                <v-card-title>
                                    Editar estudiante
                                </v-card-title>
                                <v-form>
                                    <v-container>
                                        <v-row>
                                            <v-col
                                                    cols="12"
                                                    md="4"
                                            >
                                                <v-text-field
                                                        v-model="editedItem.name"
                                                        label="Nombre"
                                                        required
                                                />
                                            </v-col>

                                            <v-col
                                                    cols="12"
                                                    md="4"
                                            >
                                                <v-text-field
                                                        v-model="editedItem.last_name"
                                                        label="Apellido paterno"
                                                        required
                                                />
                                            </v-col>

                                            <v-col
                                                    cols="12"
                                                    md="4"
                                            >
                                                <v-text-field
                                                        v-model="editedItem.second_name"
                                                        label="Apellido materno"
                                                        required
                                                />
                                            </v-col>

                                            <v-col
                                                    cols="12"
                                                    md="4"
                                            >
                                                <v-text-field
                                                        v-model="editedItem.rut"
                                                        :counter="12"
                                                        label="Rut"
                                                        v-mask="'##.###.###-X'"
                                                        required
                                                        disabled
                                                />
                                            </v-col>

                                            <v-col
                                                    cols="12"
                                                    md="4"
                                            >
                                                <v-autocomplete
                                                    label="Carrera"
                                                    :items="items"
                                                    v-model="editedItem.career"
                                                    hint="Escriba la carrera para filtrar" 
                                                    >

                                                        <template v-slot:item="data">
                                                            <template v-if="typeof data.item !== 'object'">
                                                                <v-list-item-content v-text="data.item"></v-list-item-content>
                                                            </template>
                                                        <template v-else>
                                                            <v-list-item-content>
                                                            <v-list-item-title v-html="'Carrera: ' + data.item"></v-list-item-title>
                                                            </v-list-item-content>
                                                        </template>
                                                        </template>
                                                        
                                                </v-autocomplete>



                                            </v-col>

                                            <v-col
                                                    cols="12"
                                                    md="4"
                                            >

                                                <v-autocomplete
                                                    label="Facultad"
                                                    :items="items2"
                                                    v-model="editedItem.department"
                                                    hint="Escriba la facultad para filtrar" 
                                                    >

                                                        <template v-slot:item="data">
                                                            <template v-if="typeof data.item !== 'object'">
                                                                <v-list-item-content v-text="data.item"></v-list-item-content>
                                                            </template>
                                                        <template v-else>
                                                            <v-list-item-content>
                                                            <v-list-item-title v-html="'Facultad: ' + data.item"></v-list-item-title>
                                                            </v-list-item-content>
                                                        </template>
                                                        </template>
                                                        
                                                </v-autocomplete>
                                            </v-col>


                                            <v-col
                                                    cols="12"
                                                    md="4"
                                            >
                                                <v-btn text color="blue" v-on:click="editStudent">
                                                    <v-icon>mdi-account-plus</v-icon>
                                                    Guardar
                                                </v-btn>
                                            </v-col>
                                        </v-row>
                                    </v-container>
                                </v-form>
                            </v-card>
                        </v-dialog>
                    </v-toolbar>
                </template>

                <template v-slot:item.active="{ item }">
                    <v-chip color="red" v-if="item.active === false"> <v-icon x-small color="white">mdi-close</v-icon>  </v-chip>
                    <v-chip color="green" v-else-if="item.active === true"> <v-icon x-small color="white">mdi-check</v-icon></v-chip>
                </template>

                <template v-slot:item.action="{ item }">
                    <v-tooltip bottom>
                        <template v-slot:activator="{ on }">
                            <v-icon
                                    small
                                    class="mr-2"
                                    @click="editItem(item)"
                                    v-on="on"
                            >
                                edit
                            </v-icon>
                        </template>
                        <span> Editar </span>
                    </v-tooltip>

                    <v-tooltip bottom>
                        <template v-slot:activator="{ on }">
                            <v-icon
                                    small
                                    class="mr-2"
                                    @click="deleteItem(item)"
                                    v-on="on"
                            >
                                mdi-swap-horizontal-bold
                            </v-icon>
                        </template>
                        <span> Editar </span>
                    </v-tooltip>

                </template>

                <template v-slot:no-data>
                    <v-btn color="primary" @click="initialize">Reset</v-btn>
                </template>

            </v-data-table>
</template>

<script>
    import {mapState,mapMutations}  from 'vuex'
    import axios from "axios";
    export default {
        data: () => ({
            //variables de agregar estudiante
            json: {
                rut: null,
                name: null,
                last_name: null,
                second_name: null,
                career: null,
                department: null,
                active: true
            },
            message: {
                rut: "El campo de rut se encuentra vaco.",
                rutLength: "El largo del rut debe ser de 12",
                name: "El campo de nombre se encuentra vaco.",
                department: "El campo de departamento no ha sido seleccionado.",
                last_name: "El campo de apellido paterno se encuentra vaco.",
                second_name: "El campo de apellida materno se encuentra vaco.",
                career: "El campo de carrera no ha sido seleccionado.",
                nice: "El estudiante ha sido agregado exitosamente.",
                exist: "El rut ingresado ya existe."
            },
                items: [
                    "INGENIERA CIVIL EN ELECTRICIDAD"
                    ,"INGENIERA CIVIL EN GEOGRAFA"
                    ,"INGENIERA CIVIL EN INDUSTRIA"
                    ,"INGENIERA CIVIL EN INFORMTICA"
                    ,"INGENIERA CIVIL EN MECNICA"
                    ,"INGENIERA CIVIL EN METALURGIA"
                    ,"INGENIERA CIVIL EN MINAS"
                    ,"INGENIERA CIVIL EN OBRAS CIVILES"
                    ,"INGENIERA CIVIL EN QUMICA"
                    ,"INGENIERA CIVIL BIOMDICA"
                    ,"INGENIERA CIVIL EN TELEMTICA"
                    ,"INGENIERA CIVIL MECATRNICA"
                    ,"INGENIERA CIVIL EN BIOTECNOLOGA"
                    ,"INGENIERA CIVIL EN AMBIENTE"
                    ,"INGENIERA DE EJECUCIN EN ELECTRICIDAD"
                    ,"INGENIERA DE EJECUCIN EN GEOMENSURA"
                    ,"INGENIERA DE EJECUCIN EN INDUSTRIA"
                    ,"INGENIERA DE EJECUCIN EN COMPUTACIN E INFORMTICA"
                    ,"INGENIERA DE EJECUCIN EN MECNICA"
                    ,"INGENIERA DE EJECUCIN EN METALURGIA"
                    ,"INGENIERA DE EJECUCIN EN MINAS"
                    ,"INGENIERA DE EJECUCIN EN QUMICA"
                    ,"INGENIERA DE EJECUCIN EN CLIMATIZACIN (CALEFACCIN, REFRIGERACIN Y AIRE ACONDICIONADO)"
                    ,"BACHILLERATO EN CIENCIAS Y HUMANIDADES"
                    ,"ADMINISTRACIN PBLICA"
                    ,"INGENIERA COMERCIAL"
                    ,"CONTADOR PBLICO Y AUDITOR (DIURNO)"
                    ,"CONTADOR PBLICO Y AUDITOR (VESPERTINO)"
                    ,"INGENIERA FSICA"
                    ,"INGENIERA MATEMTICA"
                    ,"INGENIERA ESTADSTICA"
                    ,"LICENCIATURA EN CIENCIA DE LA COMPUTACIN"
                    ,"PEDAGOGA EN MATEMTICA Y COMPUTACIN"
                    ,"PEDAGOGA EN FSICA Y MATEMTICA"
                    ,"BIOQUMICA Y LICENCIATURA EN BIOQUMICA"
                    ,"QUMICA Y LICENCIATURA EN QUMICA"
                    ,"PEDAGOGA EN QUMICA Y BIOLOGA"
                    ,"TCNICO UNIVERSITARIO EN ANLISIS QUMICO Y FSICO (DIURNO)"
                    ,"TCNICO UNIVERSITARIO EN ANLISIS QUMICO Y FSICO (VESPERTINO)"
                    ,"QUMICA Y FARMACIA"
                    ,"PEDAGOGA EN CASTELLANO"
                    ,"PEDAGOGA EN INGLS"
                    ,"PEDAGOGA EN FILOSOFA"
                    ,"LICENCIATURA EN HISTORIA"
                    ,"PEDAGOGA EN HISTORIA Y CIENCIAS SOCIALES"
                    ,"PEDAGOGA EN EDUCACIN GENERAL BSICA"
                    ,"LICENCIATURA EN LINGSTICA APLICADA A LA TRADUCCIN EN INGLS - JAPONS E INGLS - PORTUGUS"
                    ,"LICENCIATURA EN ESTUDIOS INTERNACIONALES"
                    ,"PERIODISMO"
                    ,"PSICOLOGA"
                    ,"ARQUITECTURA"
                    ,"PUBLICIDAD"
                    ,"INGENIERA DE ALIMENTOS"
                    ,"INGENIERA EN AGRONEGOCIOS"
                    ,"TECNOLOGA EN ADMINISTRACIN DE PERSONAL"
                    ,"TECNOLOGA EN ALIMENTOS"
                    ,"TECNOLOGA EN CONSTRUCCIONES"
                    ,"TECNOLOGA EN CONTROL INDUSTRIAL"
                    ,"TECNOLOGA EN DISEO INDUSTRIAL"
                    ,"TECNOLOGA EN AUTOMATIZACIN INDUSTRIAL"
                    ,"TECNOLOGA EN MANTENIMIENTO INDUSTRIAL"
                    ,"TECNOLOGA EN TELECOMUNICACIONES"
                    ,"DISEO EN COMUNICACIN VISUAL"
                    ,"MEDICINA"
                    ,"ENFERMERA"
                    ,"OBSTETRICIA Y PUERICULTURA"
                    ,"PEDAGOGA EN EDUCACIN FSICA"
                    ,"LICENCIATURA EN CIENCIAS DE LA ACTIVIDAD FSICA / ENTRENADOR DEPORTIVO"
                    ,"LICENCIATURA EN CIENCIAS DE LA ACTIVIDAD FSICA / TERAPEUTA EN ACTIVIDAD FSICA Y SALUD"
                    ,"KINESIOLOGA"
                    ,"TERAPIA OCUPACIONAL"
                    ,"DERECHO"],

            items2: ["FACULTAD DE INGENIERA", "PROGRAMA DE BACHILLERATO", "FACULTAD DE ADMINISTRACIN Y ECONOMA", 
                     "FACULTAD DE CIENCIA","FACULTAD DE QUMICA Y BIOLOGA","FACULTAD DE HUMANIDADES","ESCUELA DE ARQUITECTURA",
                     "FACULTAD TECNOLGICA","FACULTAD DE CIENCIAS MDICAS","FACULTAD DE DERECHO"],
            messageError: null,
            issue: false,

            dialogEdit: false,


            valid : true,
            search: '',
            dialog: false,
            load: true,
            disabledCorrectionRol: true,
            show: false,
            headers: [
                { text: 'Nombre', align: 'center', value: 'name' },
                { text: '1 Apellido', value: 'last_name', align: 'center' },
                { text: '2 Apellido', value: 'second_name', align: 'center' },
                { text: 'Rut', value: 'rut', align: 'center' },
                { text: 'Carrera', value: 'career', align: 'center' },
                { text: 'Estado', value: 'active', align: 'center'},
                { text: 'Acciones', value: 'action', align: 'center',sortable: false }
            ],
            rutRules: [
                v => !!v || 'Campo requerido',
                v => (v && v.length >= 11 && v.length <= 12) || 'Rut debe tener formato X.XXX.XXX-X o XX.XXX.XXX-X '
            ],
            textRules: [
                v => !!v || 'Campo requerido'
            ],
            correctionRules:[],
            plataformRules:
                [
                    v => !!v || 'Campo requerido'
                ],
            users: [],
            editedIndex: -1,
            editedItem: {
                name: '',
                last_name: '',
                second_name: '',
                rut: '',
                career: '',
                department: ''
            },
            defaultItem: {
                name: '',
                last_name: '',
                second_name: '',
                rut: '',
                career: '',
                department: ''

            },
        }),


        computed: {
            ...mapState(['allStudents','serverRoute']),
            formTitle () {
                return this.editedIndex === -1 ? 'Nuevo usuario' : 'Editar usuario'
            }
        },

        watch: {
            dialog (val) {
                val || this.close()
            },

            allStudents: function()
            {
                this.users = this.allStudents;
                this.load = false
            }
        },

        created () {
            this.$store.dispatch('getAllStudents');
        },

        methods: {
            ...mapMutations(['getAllStudents', 'changeStudentState']),
            reviewData(){
                // eslint-disable-next-line no-console
                //console.log("json", this.json.name, "issue", this. issue)
                if (this.json.name == null){

                    this.issue = true;
                    this.messageError = this.message.name
                    // eslint-disable-next-line no-console
                    //console.log("entreeeee  ", "json", this.json.name, "issue", this. issue, this.messageError)
                }
                else if (this.json.last_name == null){
                    this.issue = true;
                    this.messageError = this.message.last_name;
                }
                else if (this.json.department == null) {
                    this.issue = true
                    this.messageError = this.message.department
                }
                else if (this.json.second_name == null){
                    this.issue = true;
                    this.messageError = this.message.second_name;
                }
                else if (this.json.rut == null){
                    this.issue = true;
                    this.messageError = this.message.rut;
                }
                else if (this.json.career == null){
                    this.issue = true;
                    this.messageError = this.message.career;
                }
                else if (this.json.rut.length < 12){
                    this.issue = true;
                    this.messageError = this.message.rutLength;
                }
                else{
                    this.issue = false;

                    for (let i=0; i< this.allStudents.length; i++)
                    {
                        if(this.allStudents[i].rut === this.json.rut)
                        {
                            this.issue = true;
                            this.messageError = this.message.exist;
                            break;
                        }
                    }
                }
            },
            sleep (time) {
                return new Promise((resolve) => setTimeout(resolve, time));
            },
            postStudent() {
                this.reviewData();
                if (this.issue === false) {
                    axios.post(this.serverRoute + '/student/post', this.json)
                        .then(response => {
                            // eslint-disable-next-line no-console
                            console.log(response);
                        })
                        .catch(function (error) {
                            // eslint-disable-next-line no-console
                            console.log(error);
                        });
                    this.sleep(500).then(() => {
                        this.pushRestore()
                        this.dialog = false
                    });
                }
            },
            editStudent() {
                axios.post(this.serverRoute + '/student/edit', this.editedItem)
                    .then(response => {
                        this.$store.dispatch('getAllStudents');
                        // eslint-disable-next-line no-console
                        console.log(response);
                    })
                    .catch(function (error) {
                        // eslint-disable-next-line no-console
                        console.log(error);
                    });
                this.sleep(500).then(() => {
                    this.dialogEdit = false
                });
            },
            pushRestore(){
                this.allStudents.push(this.json)
                // eslint-disable-next-line no-console
                //console.log("miaaaaaau", this.json.rut)
                this.json ={
                    rut: null,
                    name: null,
                    last_name: null,
                    second_name: null,
                    career: null,
                    department: null,
                    active: true
                }
            },
            editItem (item) {
                this.editedIndex = this.users.indexOf(item)
                this.editedItem = Object.assign({}, item)
                this.dialogEdit = true
                // eslint-disable-next-line no-console
                //console.log("Soy item",item)
            },
            close () {
                this.dialog = false
                setTimeout(() =>
                {
                    this.editedItem = Object.assign({}, this.defaultItem)
                    this.editedIndex = -1
                }, 300)
            },
            deleteItem (item) {
                this.editedIndex = this.users.indexOf(item);
                this.editedItem = Object.assign({}, item);

                if (item.active === true)
                {
                    confirm('Seguro que desea deshabilitar al estudiante ' + item.name + '?') && this.changeStudentState(this.editedItem)

                }
                else
                {
                    confirm('Seguro que desea volver a habilitar al estudiante ' + item.name + '?') && this.changeStudentState(this.editedItem)
                }
            },
            changeStudentState(data)
            {
                axios.post(this.serverRoute + '/student/changestate/', data)
                    .then(response => {
                        this.$store.dispatch('getAllStudents')
                        // eslint-disable-next-line no-console
                        console.log(response);
                    })
                    .catch(error => {
                        // eslint-disable-next-line no-console
                        console.log(error)
                    })
            }

        },
    }
</script>

<style>

</style>